import React from 'react';
import {observer} from 'mobx-react';
import {Link} from 'react-router-dom';
import {Table, Button, Divider, message, Modal, Pagination,Tabs} from 'antd';
import {requestPost} from "@/utils/api";
import Config from "@/config";
import Schemas from "@/utils/Schemas/construction";
import * as mobx from "mobx";
import AddOrEdit from "@/components/construction/AddOrEdit";
//import TechnologicalMethod from "@/components/construction/TechnologicalMethod";
import ConstructionStore from 'store/construction/ConstructionStore';

const confirm = Modal.confirm;
const {TabPane} = Tabs;

@observer
class Construction extends React.Component {
  constructor(props) {
    super(props);
    this.store = new ConstructionStore()
    this.state = {
      selectedRowKeys: [],
      loading: false,
      visible: false,
      title: '新增',
      cardVisible: false,
      cardTitle: ''
    }
  }

  componentDidMount() {
    this.getTableData();
     this.store.getProjectZPlanList();
  }

  componentWillUnmount() {

  }

  // 查询列表
  getTableData = () => {
    this.setState({loading:true});
    requestPost('getProjectList',Config.construction.getProjectList, Schemas.construction.getProjectList, {
      text:"",
      pageIndex: this.store.pageIndex,
      pageSize: this.store.pageSize
    }).then((res) => {
      const data = res;
      if (Array.isArray(data.body)&&data.body.length>0) {
        this.store.tableData = Object.assign([], data.body.map((item,index)=>{
          return Object.assign(item,{key: index+1})
        }));
        this.store.pageIndex = data.pageIndex;
        this.store.pageSize = data.pageSize;
        this.store.totalCount = data.totalCount;
        this.setState({loading:false});
      }
    });
  }

  // 删除
  showDeleteConfirm = () => {
    let _this = this;
    const {selectedRowKeys} = _this.state;
    if (selectedRowKeys.length==0) {
       message.warn("请先选中一项!");
       return false;
    }
    confirm({
      title: '您确定删除吗?',
      okText: '确定',
      okType: 'danger',
      cancelText: '取消',
      onOk() {
        requestPost('delProject',Config.construction.delProject, Schemas.construction.delProject, {
          projectIds: selectedRowKeys
        }).then((res) => {
          if (res.code==1) {
            message.success("删除成功!");

            _this.store.pageIndex = 1;
            _this.store.pageSize = 10;
            _this.getTableData();
            _this.setState({
              selectedRowKeys:[]
            });

          } else {
            message.error('删除失败!');
          }
        })
      },
      onCancel() {
        //console.log('Cancel');
      },
    });
  }

  // 新增
  handleAdd = () => {
    // 新建项目
    requestPost('addProject', Config.construction.addProject, Schemas.construction.addProject, {}).then((res)=>{
      this.store.projectField = Object.assign({}, res, {
        positions: res.positions==null?[]:res.positions
      });
      this.setState({
        visible: true,
        title: '新增'
      });
    });
  }

  // 编辑
  handleEdit = (item) => {
    let _this = this;
    requestPost('getProject', Config.construction.getProject, Schemas.construction.getProject, {
      projectId: item
    }).then((res)=>{
      _this.store.buildUnits = res.buildUnits.orgUser==null?[]:res.buildUnits.orgUser;
      _this.store.supervisionUnits = res.supervisionUnits.orgUser==null?[]:res.supervisionUnits.orgUser;
      _this.store.constructionUnits = res.constructionUnits.orgUser==null?[]:res.constructionUnits.orgUser;
      const sub = res.subcontractors == null?[]:res.subcontractors;
      _this.store.projectField = Object.assign(res, {subcontractors: sub}, {positions: res.positions==null?[]:res.positions});
      //{state: this.convertState2String(res.state)}
      _this.setState({
        visible: true,
        title: '编辑',
      },()=>{
        if (res.wisdomseeProjectId!="") {
          this.child.getProjectInfoById(res.wisdomseeProjectId);
        }
      });
    });
  }

  showSizeChange = async (current, pageSize) => {
    this.store.pageIndex = 1;
    this.store.pageSize = pageSize;
    this.getTableData();
  }

  onChange = async (pageNumber) => {
    this.store.pageIndex = pageNumber;
    this.getTableData();
  }

  // 列选
  onSelectChange = selectedRowKeys => {
    this.setState({ selectedRowKeys });
  };

  updateState = (type, data) => {
    this.setState({
      [type]: data
    })
  }

  onClose = () => {
    this.setState({
      cardVisible: false,
    });
  };

  convertState2String =(type)=> {
		if(type===1) return "在建"
		if(type===2) return "维护"
	}

  onRef = (ref) => {
    this.child = ref;
  }

  render() {
    let pathname = this.props.match.path;
    const columns = [
      {
        title: '序号',
        dataIndex: 'key',
        key: 'key',
        align: 'center',
        width: '50px'
      },
      {
        title: '项目编号',
        dataIndex: 'code',
        key: 'code',
        align: 'center',
      },
      {
        title: '项目名称',
        dataIndex: 'name',
        key: 'name',
        render:(text, record) => (
          <a
              style={{
                wordWrap:'break-word',
                whiteSpace: 'pre-wrap'
              }}
              href="javascript:;" onClick={this.handleEdit.bind(this, record.id)}>{text}</a>
        ),
        align: 'center',
      },
    {
        title: '建设单位',
        dataIndex: 'buildUnits',
        key: 'buildUnits',
        render:(text, record) => (
          <div style={{
            wordWrap:'break-word',
            whiteSpace: 'pre-wrap'
          }} >{text==null?"":text.name}</div>
        ),
      align: 'center',
    },
      {
        title: '施工单位',
        dataIndex: 'constructionUnits',
        key: 'constructionUnits',
        render:(text, record) => (
          <div style={{
            wordWrap:'break-word',
            whiteSpace: 'pre-wrap'
          }}>{text==null?"":text.name}</div>
        ),
        align: 'center',
      },
      {
        title: '监理单位',
        dataIndex: 'supervisionUnits',
        key: 'supervisionUnits',
        render:(text, record) => (
          <div style={{
            wordWrap:'break-word',
            whiteSpace: 'pre-wrap'
          }}>{text==null?"":text.name}</div>
        ),
        align: 'center',
      },
      {
        title: '操作',
        key: 'action',
        align: 'left',
        width: '190px',
        fixed: 'right',
        render: (text, record) => (
          <div>
            <Link style={{color:'#1890ff'}} className="pointer" to={"/schedule?projectId="+record.id+"&name="+encodeURI(record.name)}>排期</Link>
            <Divider type="vertical"/>
            <Link style={{color:'#1890ff'}} className="pointer" to={"/xmgl?projectId="+record.id+"&name="+encodeURI(record.name)}>日志</Link>
            <Divider type="vertical"/>
            <Link style={{color:'#1890ff'}} className="pointer" to={"/weeknews?projectId="+record.id+"&name="+encodeURI(record.name)}>周报</Link>
            {/*<Divider type="vertical"/>
            <Link style={{color:'#1890ff'}} className="pointer" target="_blank" to={"/bi?projectId="+record.id}>BI</Link>*/}
          </div>
        ),
      }
    ];
    const { visible, loading, selectedRowKeys, title } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };

    return (
      <div className='w'>
        <Button style={{margin:'0 0 10px 0'}} type="primary" onClick={this.handleAdd}>新增</Button>
        <Divider type="vertical" />
        <Button style={{margin:'0 0 10px 0'}} type="danger" onClick={this.showDeleteConfirm.bind(this)}>删除</Button>
        <Table columns={columns}
               dataSource={mobx.toJS(this.store.tableData)}
               pagination={false}
               rowKey={record =>record.id}
               rowSelection={rowSelection}
               loading={loading}
               size="middle"
        />
        <Pagination
            style={{margin: '20px', textAlign: 'right'}}
            current={this.store.pageIndex}
            pageSize={this.store.pageSize}
            total={this.store.totalCount}
            showSizeChanger
            onShowSizeChange={this.showSizeChange}
            onChange={this.onChange}
            showLessItems={true}
            showQuickJumper/>
       {/* <Tabs  key="1"
               className="admin-tabs"
               tabPosition={'left'}
        >
          <TabPane tab="项目管理" key="1">

          </TabPane>
          <TabPane tab="工艺工法库" key="2">
            <TechnologicalMethod
              store={this.store}/>
          </TabPane>
        </Tabs>*/}
        <AddOrEdit
          visible={visible}
          title={title}
          updateState={this.updateState}
          store={this.store}
          getTableData={this.getTableData}
          onRef={this.onRef}
        />
      </div>
    )
  }
}

export default Construction;



