import React from 'react';
import {observer} from 'mobx-react';
import {
	Row,
	Col,
	message,
	Icon,
	Form,
	Input,
	Divider,
	Button,
	Select,
	Radio,
	Modal,
	Cascader,
	DatePicker,
	InputNumber,
	Spin
} from 'antd';
import * as mobx from "mobx";
import cityData3 from "@/utils/cityData";
import AsyncSelect from "@/components/common/AsyncSelectOld";
import Config from "@/config";
import Schemas from "@/utils/Schemas/authorization";
import {List} from 'immutable';
import moment from 'moment';

const {Option} = Select;
const RadioGroup = Radio.Group;

@observer
class AddOrEdit extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			state: ['在建', '维护'],//项目状态1在建2完工3停工
			itemState: [{id: 1, name: '在建'}, {id: 2, name: '维护'}],
			buildUnits: [], // 建设单位
			supervisionUnits: [], // 施工单位
			constructionUnits: [], // 监理单位
			pos: ['负责人', '专业质检员', '专业工程师', '专业工长', '检验员'], // 岗位
			fetching: false,
			text:"", //关键字
			pageIndex:1,
			companyData:[]
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
		this.setState({
			text:"", //关键字
			pageIndex:1,
		},()=>{
			this.getCompanyList(); // 调用api方法
		})
		this.props.onRef(this);
	}

	componentWillUnmount() {

	}

	convertProjectState2Int = (type) => {
		if (type === "在建") return 1
		if (type === "维护") return 2
		if (type === "停工") return 3
	}

	handleInputNumberChage = (type, number) => {
		this.store.projectField[type] = number;
	}

	// 高德地图地理编码将地址转化成经纬度
	onSearchAddress = (param) => {
		fetch('https://restapi.amap.com/v3/geocode/geo?key=08cde7c6ec084c2b876f82636820b573&address=' + param, {
			method: 'GET'
		})
		.then(res => {
			if (res.ok) {
				res.json().then(data => {
					let list = data.geocodes;
					let geocodes = list[0]['location'].split(',');
					this.store.projectField = Object.assign(mobx.toJS(this.store.projectField), {
						longitude: parseFloat(geocodes[0]),
						latitude: parseFloat(geocodes[1])
					});
				})
			}
		})
	}

	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (!err) {
				this.store.projectField['buildUnits'] = Object.assign(this.store.projectField['buildUnits'], {orgUser: this.store['buildUnits']});
				this.store.projectField['supervisionUnits'] = Object.assign(this.store.projectField['supervisionUnits'], {orgUser: this.store['supervisionUnits']});
				this.store.projectField['constructionUnits'] = Object.assign(this.store.projectField['constructionUnits'], {orgUser: this.store['constructionUnits']});
				//this.store.projectField['state'] = this.convertProjectState2Int(this.store.projectField['state']);
				this.store.projectField['requiredTime'] = moment(this.store.projectField['requiredTime']).valueOf();
				this.store.saveProject().then((res) => {
					if (res.code == 0) {
						message.error('保存失败')
					} else {
						message.success('保存成功')
						this.handleCancel();
						this.store.pageIndex = 1;
						this.store.pageSize = 20;
						this.props.getTableData();
					}
				});
			}
		})
	}

	handleCancel = () => {
		this.props.updateState('visible', false);
		this.store.buildUnits = Object.assign([]);
		this.store.supervisionUnits = Object.assign([]);
		this.store.constructionUnits = Object.assign([]);
		this.store.projectField = Object.assign({}, {
			id: null,
			name: '',
			code: '',
			region: '',
			address: '',
			buildUnits: {
				"id": "",
				"name": "",
				"type": 1,
				"orgUser": []
			},
			supervisionUnits: {
				"id": "",
				"name": "",
				"type": 2,
				"orgUser": []
			},
			constructionUnits: {
				"id": "",
				"name": "",
				"type": 3,
				"orgUser": []
			},
			subcontractors: [],
			foreman: '',
			isSave: 1,
			createTime: null,
			isDeleted: 1,
			longitude: null,
			latitude: null,
			positions: [],
			zplan: null,
			wisdomseeProjectId: ''
		});
		this.props.form.setFields({
			id: null,
			name: '',
			code: '',
			region: '',
			address: '',
			buildUnits: {
				"id": "",
				"name": "",
				"type": 1,
				"orgUser": []
			},
			supervisionUnits: {
				"id": "",
				"name": "",
				"type": 2,
				"orgUser": []
			},
			constructionUnits: {
				"id": "",
				"name": "",
				"type": 3,
				"orgUser": []
			},
			subcontractors: [],
			foreman: '',
			isSave: 1,
			createTime: null,
			isDeleted: 1,
			longitude: null,
			latitude: null,
			positions: [],
			zplan: null,
			wisdomseeProjectId: ''
		})
		this.props.form.resetFields();
		this.setState({
			text: '',
			pageIndex: 1
		})
	}

	onchange = (type, e) => {
		this.store.projectField[type] = Object.assign(this.store.projectField[type], {name: e.target.value});
	}

	handChange = (type, e) => {
		this.store.projectField[type] = e.target.value;
	}

	handNumber = (type, value) => {
		this.store.projectField[type] = value;
	}

	// 地区选择
	cityChange = async (type, value) => {
		this.store.projectField[type] = Object.assign([], value);
		//console.log(value, value.join(""), '地区')
		if (value && value.length) {
			const param = value.join("");
			// 获取经纬度
			this.onSearchAddress(param);
		}
	}

	// 地区筛选
	filter = (inputValue, path) => {
		return path.some(option => option.label.toLowerCase().indexOf(inputValue.toLowerCase()) > -1);
	}

	// 删除字段
	remove = (type, index) => {
		let customList = JSON.parse(JSON.stringify(this.store[type]));
		let list = List(customList);
		this.store[type] = Object.assign([], list.delete(index).toJS())
		this.props.form.resetFields();
	};

	// 增加字段
	add = (type) => {
		let customList = JSON.parse(JSON.stringify(this.store[type]));
		this.store[type] = [...customList, {userId: '', userName: '', position: ''}]
		this.props.form.resetFields();
	};

	onSelChange = (type, index, value, e) => {
		this.props.form.resetFields();
		const newVal = e.props.val;
		let customList = JSON.parse(JSON.stringify(this.store[type]));
		let list = List(customList);
		list.update(index, (val) => {
			val.position = customList[index].position;
			val.userId = newVal.id;
			val.userName = newVal.realname;
			return val;
		});
		this.store[type] = Object.assign([], list.toJS())
	}

	handleChange = (type, index, value) => {
		this.props.form.resetFields();
		let customList = JSON.parse(JSON.stringify(this.store[type]));
		let list = List(customList);
		list.update(index, (val) => {
			val.position = value;
			val.userId = customList[index].userId;
			val.userName = customList[index].userName;
			return val;
		});
		this.store[type] = Object.assign([], list.toJS())
	}

	handleChangeState = (type, e) => { //问题状态的修改
		this.store.projectField[type] = e.target.value;
	}

	onDateChange = (type, value, dateString) => {
		this.store.projectField[type] = dateString;
	}

	// 添加分包单位
	handleAdd = (type) => {
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		if (customList.some((item) => {
			return item.name == "" || item.name == null
		})) {
			message.warn("请填写分包单位")
			return false;
		}
		this.store.projectField[type] = [...customList, {
			"id": null,
			"name": "",
			"ogranCode": "",
			"type": 1,
			"projectId": this.store.projectId,
			"orgUser": []
		}];
		this.props.form.resetFields();
	}

	// 添加分包单位成员
	handleFenbao = (type, index) => {
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		customList[index].orgUser = [...customList[index].orgUser, {userId: '', userName: '', position: ''}];
		this.store.projectField[type] = Object.assign([], customList);
		this.props.form.resetFields();
	}

	// 删除分包单位成员
	removeFenbao = (type, index, vindex) => {
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		let list = List(customList[index].orgUser);
		customList[index].orgUser = Object.assign([], list.delete(index).toJS())
		this.store.projectField[type] = Object.assign([], customList);
		this.props.form.resetFields();
	};

	// 分包单位变化
	onFenbaochange = (type, index, e) => {
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		customList[index].name = e.target.value;
		this.store.projectField[type] = Object.assign([], customList);
		this.props.form.resetFields();
	}

	// 分包单位成员岗位名称
	handlefenbaoChange = (type, index, vindex, value) => {
		//console.log(type, index, vindex, value, '变化')

		//this.props.form.resetFields();
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		let list = List(customList[index].orgUser);
		list.update(vindex, (val) => {
			val.position = value;
			val.userId = customList[index].orgUser[vindex].userId;
			val.userName = customList[index].orgUser[vindex].userName;
			return val;
		});
		customList[index].orgUser = Object.assign([], list.toJS());
		this.store.projectField[type] = Object.assign([], customList);
		this.props.form.resetFields();
		//console.log(JSON.parse(JSON.stringify(this.store.projectField[type])), '变化1')
	}

	// 分包单位成员姓名
	onfenbaoSelChange = (type, index, vindex, value, e) => {
		//console.log(type, index, vindex, value, e, '888')

		const newVal = e.props.val;
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		let list = List(customList[index].orgUser);
		list.update(vindex, (val) => {
			val.position = customList[index].orgUser[vindex].position;
			val.userId = newVal.id;
			val.userName = newVal.realname;
			return val;
		});
		customList[index].orgUser = Object.assign([], list.toJS());
		this.store.projectField[type] = Object.assign([], customList);
		this.props.form.resetFields();
		//	console.log(JSON.parse(JSON.stringify(this.store.projectField[type])), '变化2')
	}

	// 删除分包单位
	removeFen = (type, index) => {
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		let list = List(customList);
		this.store.projectField[type] = Object.assign([], list.delete(index).toJS())
		//console.log( JSON.parse(JSON.stringify(this.store.projectField[type])), '变化2')

		this.props.form.resetFields();
	}

	// 限制数字
	limitDecimals = (value) => {
		const reg = /^(\-)*(\d+)\.(\d\d).*$/;
		if (typeof value === 'string') {
			return !isNaN(Number(value)) ? value.replace(reg, '$1$2.$3') : ''
		} else if (typeof value === 'number') {
			return !isNaN(value) ? String(value).replace(reg, '$1$2.$3') : ''
		} else {
			return ''
		}
	};

	// 检查人员总数
	checkPeopleCount = (rule, value, callback) => {
		if (value > 0) {
			callback();
			return;
		} else {
			callback('人员数量必须大于0')
		}
	}

	// 添加定点位置
	handleAddPosition = (type) => {
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		if (customList.some((item) => {
			return item.name == "" || item.name == null
		})) {
			message.warn("请填写位置点名称")
			return false;
		}
		this.store.projectField[type] = [...customList, {
			"id": null,
			"name": "",
			"projectId": this.store.projectField.id,
		}];
		this.props.form.resetFields();
	}

	// 关联斑马
	zplanChange = (value, e) => {
		const {val} = e.props;
		this.store.projectField['zplan'] = Object.assign({}, val);
	}

	// 定点位置
	onPosChange = (type, index, e) => {
		let customList = JSON.parse(JSON.stringify(this.store.projectField[type]));
		customList[index].name = e.target.value;
		this.store.projectField[type] = Object.assign([], customList);
	}

	companyScroll = e => {
		e.persist();
		const {target} = e;
		if (target.scrollTop + target.offsetHeight === target.scrollHeight) {
			const {pageIndex} = this.state;
			const nextScrollPage = pageIndex + 1;
			this.setState({
				pageIndex: nextScrollPage
			},()=>{
				const {text, pageIndex, companyData} = this.state;
				const old = JSON.parse(JSON.stringify(companyData))
				const params = {
					text,
					pageIndex
				}
				this.store.getAllProjectList(params).then((res) => {
					this.setState({
						companyData: [...old, ...res.body]
					});
				});
			});
		}
	};

	fetchCompany = value => {
		this.setState({companyData: [], fetching: true, text: value, pageIndex: 1},()=>{
			this.getCompanyList(); // 关键字模糊查询api
		});
	};

	getCompanyList = () => {
		const {text, pageIndex} = this.state;
		const params = {
			text,
			pageIndex
		}
		this.store.getAllProjectList(params).then((res) => {
           this.setState({
	           companyData: Object.assign([], typeof(res.body)!=="undefined" ? res.body:[])
           });
		});
	}

	setCheckNum = (val, e) => {
		this.store.projectField['wisdomseeProjectId'] = val;
	}

	getProjectInfoById = (param) => {
		this.store.getProjectInfoById(param).then((res) => {
			if (res ) {
				this.setState({
					companyData: [{
						id: param,
						name: res.name
					}]
				});
			}
		});
	}

	render() {
		const {getFieldDecorator} = this.props.form;
		const {itemState, companyData, fetching} = this.state;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			}
		}
		const formItemLayoutWithOutLabel = {
			wrapperCol: {
				xs: {span: 24, offset: 0},
				sm: {span: 20, offset: 4},
			}
		};
		const formItems = mobx.toJS(this.store.buildUnits).length && mobx.toJS(this.store.buildUnits).map((item, index) => {
			return <Row key={'buildUnits' + index}>
				<Col span={12}>
					<Form.Item
						label={"岗位名称"}
						labelCol={{
							xs: {span: 24},
							sm: {span: 8}
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 16}
						}}
					>
						{getFieldDecorator("position" + index, {
							initialValue: item.position || "",
							rules: [
								{
									required: mobx.toJS(this.store.buildUnits).length ? true : false,
									message: "请输入岗位名称",
								},
							],
						})(<Select
							value={item.position}
							onChange={this.handleChange.bind(this, "buildUnits", index)}>
							{this.state.pos.map((vitem, vindex) => {
								return (<Option key={'pos' + vindex} value={vitem}>{vitem}</Option>)
							})}
						</Select>)}
					</Form.Item>
				</Col>
				<Col span={12} className="pr">
					<Form.Item
						label={'姓名'}
						labelCol={{
							xs: {span: 24},
							sm: {span: 6}
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 14}
						}}
					>
						{getFieldDecorator("userName" + index, {
							initialValue: item.userName || "",
							rules: [
								{
									required: mobx.toJS(this.store.buildUnits).length ? true : false,
									message: "请输入姓名",
								},
							],
						})(<AsyncSelect
							type="userid"
							url={Config.authorization.getUserInfoListByRoleCode}
							onChange={this.onSelChange.bind(this, 'buildUnits', index)}
							value={item.userName}
							query={Schemas.authorization.getUserInfoListByRoleCode}
						/>)}
						<Button
							shape="circle"
							className="dynamic-delete-button2"
							onClick={this.remove.bind(this, "buildUnits", index)}
							size="small"
							icon="minus"
							type="default"/>
					</Form.Item>
				</Col>
			</Row>
		});
		const formItems2 = mobx.toJS(this.store.supervisionUnits).length && mobx.toJS(this.store.supervisionUnits).map((item, index) => {
			return <Row key={'supervisionUnits' + index}>
				<Col span={12}>
					<Form.Item
						label={"岗位名称"}
						labelCol={{
							xs: {span: 24},
							sm: {span: 8}
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 16}
						}}
					>
						{getFieldDecorator("position2" + index, {
							initialValue: item.position || "",
							rules: [
								{
									required: mobx.toJS(this.store.supervisionUnits).length ? true : false,
									message: "请输入岗位名称",
								},
							],
						})(<Select
							value={item.position}
							onChange={this.handleChange.bind(this, 'supervisionUnits', index)}>
							{this.state.pos.map((vitem, vindex) => {
								return (<Option key={'pos' + vindex} value={vitem}>{vitem}</Option>)
							})}
						</Select>)}
					</Form.Item>
				</Col>
				<Col span={12} className="pr">
					<Form.Item
						label={'姓名'}
						labelCol={{
							xs: {span: 24},
							sm: {span: 6}
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 14}
						}}
					>
						{getFieldDecorator("userName2" + index, {
							initialValue: item.userName || "",
							rules: [
								{
									required: mobx.toJS(this.store.supervisionUnits).length ? true : false,
									message: "请输入姓名",
								},
							],
						})(<AsyncSelect
							type="userid"
							url={Config.authorization.getUserInfoListByRoleCode}
							onChange={this.onSelChange.bind(this, 'supervisionUnits', index)}
							value={item.userName}
							query={Schemas.authorization.getUserInfoListByRoleCode}
						/>)}
						<Button
							shape="circle"
							className="dynamic-delete-button2"
							onClick={this.remove.bind(this, "supervisionUnits", index)}
							size="small"
							icon="minus"
							type="default"/>
					</Form.Item>
				</Col>
			</Row>
		});
		const formItems3 = mobx.toJS(this.store.constructionUnits).length && mobx.toJS(this.store.constructionUnits).map((item, index) => {
			return <Row key={'constructionUnits' + index}>
				<Col span={12}>
					<Form.Item
						label={"岗位名称"}
						labelCol={{
							xs: {span: 24},
							sm: {span: 8}
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 16}
						}}
					>
						{getFieldDecorator("position3" + index, {
							initialValue: item.position || "",
							rules: [
								{
									required: mobx.toJS(this.store.constructionUnits).length ? true : false,
									message: "请输入岗位名称",
								},
							],
						})(<Select
							value={item.position}
							onChange={this.handleChange.bind(this, 'constructionUnits', index)}>
							{this.state.pos.map((vitem, vindex) => {
								return (<Option key={'pos' + vindex} value={vitem}>{vitem}</Option>)
							})}
						</Select>)}
					</Form.Item>
				</Col>
				<Col span={12} className="pr">
					<Form.Item
						label={'姓名'}
						labelCol={{
							xs: {span: 24},
							sm: {span: 6}
						}}
						wrapperCol={{
							xs: {span: 24},
							sm: {span: 14}
						}}
					>
						{getFieldDecorator("userName3" + index, {
							initialValue: item.userName || "",
							rules: [
								{
									required: mobx.toJS(this.store.constructionUnits).length ? true : false,
									message: "请输入姓名",
								},
							],
						})(<AsyncSelect
							type="userid"
							url={Config.authorization.getUserInfoListByRoleCode}
							onChange={this.onSelChange.bind(this, 'constructionUnits', index)}
							value={item.userName}
							query={Schemas.authorization.getUserInfoListByRoleCode}
						/>)}
						<Button
							shape="circle"
							className="dynamic-delete-button2"
							onClick={this.remove.bind(this, "constructionUnits", index)}
							size="small"
							icon="minus"
							type="default"/>
					</Form.Item>
				</Col>
			</Row>
		});

		const fileds = mobx.toJS(this.store.projectField);

		return (
			<Modal
				title={this.props.title + "项目"}
				width={'60%'}
				style={{minWidth: '800px',top: 20}}
				visible={this.props.visible}
				onCancel={this.handleCancel}
				onOk={this.handleSubmit}
				maskClosable={false}
			>
				<Form {...formItemLayout}>
					<Row>
						<Col span={12}>
							<Form.Item label={`项目名称`}>
								{getFieldDecorator(`name`, {
									initialValue: fileds.name,
									rules: [
										{
											required: true,
											message: '请输入项目名称',
										},
									],
								})(<Input placeholder="请输入"
								          onChange={this.handChange.bind(this, "name")}/>)}
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item
								label={<span>
                          项目编号&nbsp;<Icon type="question-circle-o" title="项目编号保存之后自动生成"/>
                        </span>}>
								{getFieldDecorator(`code`, {
									initialValue: fileds.code,
									rules: [
										{
											required: false,
											message: '请输入项目编号',
										},
									],
								})(<Input placeholder="请输入"
								          disabled={true}
								/>)}
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item label={`项目地址`}>
								{getFieldDecorator(`region`, {
									initialValue: fileds.region == null ? [] : mobx.toJS(fileds.region),
									rules: [
										{
											required: true,
											message: '请输入项目地址',
										},
									],
								})(
									<div className="w">
										<Cascader
											placeholder="省/市/区(县)"
											showSearch={this.filter}
											options={cityData3}
											value={fileds.region == null ? [] : mobx.toJS(fileds.region)}
											onChange={this.cityChange.bind(this, 'region')}
											expandTrigger="hover"
											key="region"
										/>
									</div>
								)}
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item label={``}
							           labelCol={{
								           xs: {span: 24},
								           sm: {span: 8, offset: 0}
							           }}
							           wrapperCol={{
								           xs: {span: 24},
								           sm: {span: 16, offset: 8}
							           }}
							>
								<Input placeholder="详细地址"
								       value={fileds.address}
								       onChange={this.handChange.bind(this, "address")}/>
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item label={`关联斑马`}>
								{getFieldDecorator(`zplan`, {
									initialValue: fileds.zplan == null ? "" : (typeof fileds.zplan == 'object' ? mobx.toJS(fileds.zplan).name : ""),
									rules: [
										{
											required: false,
											message: '请选择',
										},
									],
								})(
									<Select
										showSearch
										placeholder="请选择"
										optionFilterProp="children"
										onChange={this.zplanChange}
										filterOption={(input, option) =>
											option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
										}
									>
										{mobx.toJS(this.store.ZPlanList).map((item, index) => {
											return <Option key={item.id} value={item.name}
											               val={item}>{item.name}</Option>
										})}
									</Select>
								)}
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item label={`关联看板项目`}>
								<div id="companyDiv" style={{position: 'relative'}}>
									{getFieldDecorator(`wisdomseeProjectId`, {
										initialValue: fileds.wisdomseeProjectId == null ? "" : fileds.wisdomseeProjectId,
										rules: [
											{
												required: false,
												message: '请选择',
											},
										],
									})(
										<Select
											showSearch
											placeholder="请输入项目"
											notFoundContent={fetching ? <Spin size="small"/> : null}
											onSearch={this.fetchCompany}
											onChange={this.setCheckNum}
											onPopupScroll={this.companyScroll}
											filterOption={false}
											getPopupContainer={() => document.getElementById('companyDiv')}
										>
											{companyData && companyData.map(item => {
												return (
													<Option value={item.id} key={item.id} title={item.name}>
														{item.name}
													</Option>)
											})}
										</Select>
									)}
								</div>
							</Form.Item>
						</Col>
						{/*<Col span={12}>
							<Form.Item label={`经度`}>
								{getFieldDecorator(`longitude`, {
									initialValue: fileds.longitude == null ? "" : fileds.longitude,
									rules: [
										{
											required: false,
											message: '请输入经度',
										},
									],
								})(<InputNumber
									style={{width: '100%'}}
									onChange={this.handNumber.bind(this, "longitude")}
									formatter={this.limitDecimals}
									parser={this.limitDecimals}/>)}
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item label={`纬度`}>
								{getFieldDecorator(`latitude`, {
									initialValue: fileds.latitude == null ? "" : fileds.latitude,
									rules: [
										{
											required: false,
											message: '请输入纬度',
										},
									],
								})(<InputNumber
									style={{width: '100%'}}
									onChange={this.handNumber.bind(this, "latitude")}
									formatter={this.limitDecimals}
									parser={this.limitDecimals}/>)}
							</Form.Item>
						</Col>*/}
					</Row>
					<Divider dashed={true} style={{margin: '10px 0'}}/>
					<Row>
						<Col span={12}>
							<Form.Item label={`项目状态`}>
								{getFieldDecorator(`state`, {
									initialValue: fileds.state,
									rules: [
										{
											required: false,
											message: '请选择项目状态',
										},
									],
								})(
									<div className="w">
										<RadioGroup onChange={this.handleChangeState.bind(this, "state")}
										            value={fileds.state}>
											{itemState.map((item, index) => {
												return (
													<Radio key={'state' + index} value={item.id}>{item.name}</Radio>)
											})}
										</RadioGroup>
									</div>
								)}
							</Form.Item>
						</Col>
						<Col span={12} className="mb5">
							<Form.Item
								label={`甲方要求交付日期`}
								labelCol={{
									xs: {span: 24},
									sm: {span: 12},
								}}
								wrapperCol={{
									xs: {span: 24},
									sm: {span: 12},
								}}
							>
								{getFieldDecorator(`requiredTime`, {
									initialValue: fileds.requiredTime == null ? "" : moment(fileds.requiredTime),
									rules: [
										{
											required: true,
											message: '请输入交付日期',
										},
									],
								})(
									<DatePicker
										format="YYYY-MM-DD"
										style={{width: '100%'}}
										value={fileds.requiredTime == null ? "" : moment(fileds.requiredTime)}
										onChange={this.onDateChange.bind(this, 'requiredTime')}
									/>
								)}
							</Form.Item>
						</Col>
						{fileds.state === "维护" ? (<Col span={12}>
							<Form.Item label={`使用人员总数`}>
								{getFieldDecorator('peopleCount', {
									initialValue: fileds.peopleCount,
									rules: [{required: true}, {validator: this.checkPeopleCount}],
								})(<InputNumber
									style={{width: '100%'}}
									min={0}
									onChange={this.handleInputNumberChage.bind(this, "peopleCount")}
									formatter={this.limitDecimals}
									parser={this.limitDecimals}/>)}
							</Form.Item>
						</Col>) : ""}
					</Row>
					{mobx.toJS(this.store.projectField.positions).map((item, index) => {
						return (
							<div key={'positions' + index}>
								<Row>
									<Col span={12} className="pr">
										<Form.Item label={`定点拍照位置`}>
											{getFieldDecorator(`positions+${index}`, {
												initialValue: item.name == null ? "" : item.name,
												rules: [
													{
														required: true,
														message: '请输入定点拍照位置',
													},
												],
											})(<Input placeholder="请输入"
											          value={item.name == null ? "" : item.name}
											          onChange={this.onPosChange.bind(this, "positions", index)}
											/>)}
											<Button
												shape="circle"
												className="dynamic-delete-button2"
												onClick={this.removeFen.bind(this, "positions", index)}
												size="small"
												icon="minus"
												type="default"/>
										</Form.Item>
									</Col>
								</Row>
							</div>)
					})}
					<Form.Item {...formItemLayoutWithOutLabel}>
						<Button type="dashed" onClick={this.handleAddPosition.bind(this, 'positions')}>
							<Icon type="plus"/> 添加定点位置
						</Button>
					</Form.Item>
					<Divider dashed={true} style={{margin: '10px 0'}}/>
					<Row>
						<Col span={12}>
							<Form.Item label={`建设单位`}>
								{getFieldDecorator(`buildUnits`, {
									initialValue: fileds.buildUnits == null ? "" : fileds.buildUnits.name,
									rules: [
										{
											required: true,
											message: '请输入建设单位',
										},
									],
								})(<Input placeholder="请输入"
								          value={fileds.buildUnits == null ? "" : fileds.buildUnits.name}
								          onChange={this.onchange.bind(this, "buildUnits")}/>)}
							</Form.Item>
						</Col>
					</Row>
					{formItems ? formItems : null}
					<Form.Item {...formItemLayoutWithOutLabel}>
						<Button type="dashed" onClick={this.add.bind(this, 'buildUnits')}>
							<Icon type="plus"/> 添加建设单位成员
						</Button>
					</Form.Item>
					<Divider dashed={true} style={{margin: '10px 0'}}/>
					<Row>
						<Col span={12}>
							<Form.Item label={`施工单位`}>
								{getFieldDecorator(`constructionUnits`, {
									initialValue: fileds.constructionUnits == null ? "" : fileds.constructionUnits.name,
									rules: [
										{
											required: true,
											message: '请输入施工单位',
										},
									],
								})(<Input placeholder="请输入"
								          value={fileds.constructionUnits == null ? "" : fileds.constructionUnits.name}
								          onChange={this.onchange.bind(this, "constructionUnits")}/>)}
							</Form.Item>
						</Col>
					</Row>
					{formItems3 ? formItems3 : null}
					<Form.Item {...formItemLayoutWithOutLabel}>
						<Button type="dashed" onClick={this.add.bind(this, 'constructionUnits')}>
							<Icon type="plus"/> 添加施工单位成员
						</Button>
					</Form.Item>
					<Divider dashed={true} style={{margin: '10px 0'}}/>
					<Row>
						<Col span={12}>
							<Form.Item label={`监理单位`}>
								{getFieldDecorator(`supervisionUnits`, {
									initialValue: fileds.supervisionUnits == null ? "" : fileds.supervisionUnits.name,
									rules: [
										{
											required: true,
											message: '请输入监理单位',
										},
									],
								})(<Input placeholder="请输入"
								          value={fileds.supervisionUnits == null ? "" : fileds.supervisionUnits.name}
								          onChange={this.onchange.bind(this, "supervisionUnits")}/>)}
							</Form.Item>
						</Col>
					</Row>
					{formItems2 ? formItems2 : null}
					<Form.Item {...formItemLayoutWithOutLabel}>
						<Button type="dashed" onClick={this.add.bind(this, 'supervisionUnits')}>
							<Icon type="plus"/> 添加监理单位成员
						</Button>
					</Form.Item>
					<Divider dashed={true} style={{margin: '10px 0'}}/>
					{mobx.toJS(this.store.projectField.subcontractors).map((item, index) => {
						return (
							<div key={'sub' + index}>
								<Row>
									<Col span={12} className="pr">
										<Form.Item label={`分包单位`}>
											{getFieldDecorator(`subname+${index}`, {
												initialValue: item.name == null ? "" : item.name,
												rules: [
													{
														required: true,
														message: '请输入分包单位',
													},
												],
											})(<Input placeholder="请输入"
											          value={item.name == null ? "" : item.name}
											          onChange={this.onFenbaochange.bind(this, "subcontractors", index)}/>)}
											<Button
												shape="circle"
												className="dynamic-delete-button2"
												onClick={this.removeFen.bind(this, "subcontractors", index)}
												size="small"
												icon="minus"
												type="default"/>
										</Form.Item>
									</Col>
								</Row>
								{mobx.toJS(item.orgUser == null ? [] : item.orgUser).map((vitem, vindex) => {
									return <Row key={`subname+${index}` + 'subcontractors' + vindex}>
										<Col span={12}>
											<Form.Item
												label={"岗位名称"}
												labelCol={{
													xs: {span: 24},
													sm: {span: 8}
												}}
												wrapperCol={{
													xs: {span: 24},
													sm: {span: 16}
												}}
											>
												{getFieldDecorator(`subname+${index}` + 'subcontractors' + vindex, {
													initialValue: vitem.position || "",
													rules: [
														{
															//required: item.orgUser.length?true:false,
															required: false,
															message: "请输入岗位名称",
														},
													],
												})(<Select
													value={vitem.position}
													onChange={this.handlefenbaoChange.bind(this, "subcontractors", index, vindex)}>
													{this.state.pos.map((vitem, vindex) => {
														return (<Option key={'subcontra' + vindex}
														                value={vitem}>{vitem}</Option>)
													})}
												</Select>)}
											</Form.Item>
										</Col>
										<Col span={12} className="pr">
											<Form.Item
												label={'姓名'}
												labelCol={{
													xs: {span: 24},
													sm: {span: 6}
												}}
												wrapperCol={{
													xs: {span: 24},
													sm: {span: 14}
												}}
											>
												{getFieldDecorator(`subname+${index}` + "subcontractors3" + vindex, {
													initialValue: vitem.userName || "",
													rules: [
														{
															//required: item.orgUser.length?true:false,
															required: false,
															message: "请输入姓名",
														},
													],
												})(<AsyncSelect
													type="userid"
													url={Config.authorization.getUserInfoListByRoleCode}
													onChange={this.onfenbaoSelChange.bind(this, "subcontractors", index, vindex)}
													value={vitem.userName}
													query={Schemas.authorization.getUserInfoListByRoleCode}
												/>)}
												<Button
													shape="circle"
													className="dynamic-delete-button2"
													onClick={this.removeFenbao.bind(this, "subcontractors", index, vindex)}
													size="small"
													icon="minus"
													type="default"/>
											</Form.Item>
										</Col>
									</Row>
								})}
								<Form.Item {...formItemLayoutWithOutLabel}>
									<Button type="dashed"
									        onClick={this.handleFenbao.bind(this, 'subcontractors', index)}>
										<Icon type="plus"/> 添加分包单位成员
									</Button>
								</Form.Item>
							</div>)
					})}
					<Form.Item {...formItemLayoutWithOutLabel}>
						<Button type="primary" onClick={this.handleAdd.bind(this, 'subcontractors')}>
							<Icon type="plus"/> 添加分包单位
						</Button>
					</Form.Item>
				</Form>
			</Modal>
		)
	}
}


const AddOrEditForm = Form.create({})(AddOrEdit);
export default AddOrEditForm;




